.interesting {
  margin-bottom: 15px;
  background-color: #fff;
  border-radius: 8px;
}
@media (max-width: 1024px) {
  .interesting {
    margin-bottom: 20px;
  }
}
@media (max-width: 767px) {
  .interesting {
    margin: 0 -16px 20px -16px;
    border-radius: 0;
  }
}
.interesting__wrap {
  padding: 30px 20px 14px;
}
@media (max-width: 1024px) {
  .interesting__wrap {
    padding-bottom: 10px;
  }
}
@media (max-width: 767px) {
  .interesting__wrap {
    padding-top: 20px;
    padding-bottom: 0;
  }
}
.interesting__title {
  font-weight: 500;
  font-size: 20px;
  line-height: 27px;
  color: #000000;
  margin: 0;
}
.interesting .reading-item {
  max-width: 653px;
}
.interesting .reading-item:first-child .reading-item__link {
  border-top: none;
}